import { mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
			StatusBarHeight:this.StatusBarHeight,
			StatusAddNav:this.StatusAddNav,
			type: 1,
			showBack: true,
			phone: '',
			pwd: ''
		};
	},
	onLoad() {

		uni.login({
			success: (res) => {
				// "https://api.weixing.qq.com/snsZ"
				console.log(res);
			}
		})

		
		
























	},
	methods: {
		getUserInfo(res){
			console.log(res);
		},
		// 跳转
		code_login(e) {
			this.type = 2;
		},
		pwd_login(e) {
			this.type = 1;
		},
		reset_password(e) {
			uni.navigateTo({
				url: '/pages/login/safety_monitoring/safety_monitoring'
			});
		},
		register(e) {
			uni.navigateTo({
				url: '/pages/login/register/register'
			});
		},
		login() {
			let that = this;
			if (!that.checkMobile(that.phone)) {
				that.Tips({ title: '请输入正确的手机号' });
				return;
			}
			if (that.pwd == '') {
				that.Tips({ title: '请输入密码' });
				return;
			}
			that.basePost(
				that.U({ c: 'login', a: 'login_by_mobile_pwd' }),
				{
					phone: that.phone,
					pwd: that.pwd
				},
				function(res) {
					let data=res.data;
					that.loginStore(data);
					that.Tips({ title: '登录成功！' }, { tab: 3, url: 2 });
					uni.setStorage({
						key:'data',
						data:data
					});
				},
				function(res) {
					that.Tips({ title: '账号或密码不正确。' });
				}
			);
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		...mapMutations(['loginStore'])
	}
};
